.nav{
    height: 40px;
    background-color: rgb(233, 227, 227);
    padding-top: 10px;
    padding-left: 10px;
}
.nav a{
    color: rgb(126, 223, 126);
    font-size: 15px;
    margin-right: 15px;
}
.eveList{
    border-bottom: 1px solid rgb(219, 216, 216);
    height: 40px;
    padding-top: 10px;
    line-height: 25px;
}
.headPic{
    float: left;
    width: 30px;
    height: 30px;
}
.headPic img{
    width: 30px;
    height: 30px;
    border-radius: 3px;
    margin-left: 10px;
}
.num{
    margin-left:10px;
    text-align: center;
    width: 60px;
    float: left;
    /* border: 1px solid firebrick; */
}
.replynum{
    font-size: 15px;
    color: grey;
    font-weight: bold;
}
.visitnum{
    font-size: 10px;
}
.btab{
    float: left;
    width: 40px;
    height: 20px;
    margin-right: 10px;
    margin-left: 5px;
    background-color: powderblue;
    border: 1px solid powderblue ;
}
.artTitle{
    float: left;
    width: 590px;
    overflow: hidden;
    /* border: 1px solid firebrick; */
    height: 20px;
    /* font-weight: bold; */
    margin-left: 5px;
    
}
.artTitle a{
    color: black;
}
.artTitle a:hover{
    text-decoration:underline;
}
.right{
    float: left;
    margin-left: 100px;
}
.nvaButton{
    margin:10px auto;
    width: 300px;
}
.page{
    width: 30px;
    height: 30px;
}