*{
  padding: 0;
  margin: 0;
}
ul,li{
  list-style: none;
}
body{
  background-color: rgba(236, 229, 229, 0.726);
}


/* header */
.headerwrap{
  height: 50px;
  background-color: #444;
}
.headerwrap header{
  width: 1200px;
  margin: 0 auto;
}
.headerwrap img{
  width: 120px;
  margin-top: 10px;
}
.headerwrap a{
  margin: 0 20px;
  color: #fff;
}

/* main */
.main{
  width: 1200px;
  margin: 30px auto; 
}
.content{
  width: 900px;
  float: left;
  background-color: white;
}
.sider{
  width: 270px;
  height: 500px;
  float: right;
  background-color: white;
  text-align: center;
  padding-top: 10px;
}
/* login */
.loginpage{
  margin-top: 30px;
  margin-left: 60px;
  width: 600px;
  height: 200px;
}
.loginpage div{
  margin-bottom: 20px;
}
.loginpage div button{
  margin-left: 90px;
  width: 50px;
  height: 30px;
  background-color: cornflowerblue;
  color: black;
}
/* .b-bottem{
  width: 1200px;
  margin: 0 auto;
} */
.b-bottem{
  width: 100%;
  height: 100px;
  background-color: royalblue;
  margin: 0 auto;
}